import $ from "../core";

// –∑–∞–ø—É—Å–∫ –∞–Ω–∏–º–∞—Ü–∏–∏ –∏ –≤—ã—á–∏—Å–ª–µ–Ω–∏–µ –≤—Ä–µ–º–µ–Ω–∏ —Ä–∞–±–æ—Ç—ã –∞–Ω–∏–º–∞—Ü–∏–∏
$.prototype.animate–∏OverTime = function (duration, callback, finall) {
    let timeStart;

    function _animateOverTime(time) {
        if (!timeStart) {
            timeStart = time;
        }

        let timeElapsed = time - timeStart;
        let complection = Math.min(timeElapsed / duration, 1);

        callback(complection);

        if (timeElapsed < duration) {
            requestAnimationFrame(_animateOverTime);
        } else {
            if (typeof (finall) === 'function') {
                finall();
            }
        }
    }
    return _animateOverTime;
};

// –∞–Ω–∏–º–∞—Ü–∏—è fadeIn
$.prototype.fadeIn = function (duration, display, finall) {
    for (let i = 0; i < this.length; i++) {
        this[i].style.display = display || 'block';

        const _fadeIn = (complection) => {
            this[i].style.opacity = complection;
        };

        const ani = this.animate–∏OverTime(duration, _fadeIn, finall);
        requestAnimationFrame(ani);
    }
    return this;

};

// –∞–Ω–∏–º–∞—Ü–∏—è fadeOut
$.prototype.fadeOut = function (duration, finall) {
    for (let i = 0; i < this.length; i++) {

        const _fadeOut = (complection) => {
            this[i].style.opacity = 1 - complection;
            if (complection === 1) {
                this[i].style.display = 'none';
            }
        };

        const ani = this.animate–∏OverTime(duration, _fadeOut, finall);
        requestAnimationFrame(ani);
    }
    return this;
};

//  –∞–Ω–∏–º–∞—Ü–∏—è fadeToggle
$.prototype.fadeToggle = function (duration, display, finall) {
    for (let i = 0; i < this.length; i++) {
        if (window.getComputedStyle(this[i]).display === 'none') {
            this[i].style.display = display || 'block';

            const _fadeIn = (complection) => {
                this[i].style.opacity = complection;
            };

            const ani = this.animate–∏OverTime(duration, _fadeIn, finall);
            requestAnimationFrame(ani);
        } else {
            for (let i = 0; i < this.length; i++) {

                const _fadeOut = (complection) => {
                    this[i].style.opacity = 1 - complection;
                    if (complection === 1) {
                        this[i].style.display = 'none';
                    }
                };

                const ani = this.animate–∏OverTime(duration, _fadeOut, finall);
                requestAnimationFrame(ani);
            }
        }
    }
    return this;

};